    populateData() {
        const getdata = `
        query{
            LiquorRegisters{
                    REG_LIQUOR_ID
                    REG_LIQUOR_NUMBER
                    REG_BUSINESS_ID
                    BUSINESS{
                       BUSINESS_ID
                       BUSINESS_NAME
                       CLIENTID{
                           CLIENT_ID
                           CLIENT_NAME
                           CLIENT_DOCUMENT_NUMBER
                           POSTAL_ADDRESS
                           MOBILE_NUMBER
                       }
                       SUB_COUNTY{
                           SUBCOUNTY_ID
                           SUBCOUNTY_NAME
                       }
                       WARD{
                          WARD_ID
                          WARD_NAME
                       }
                    }
                    SET_REVENUE_CATEGORY_ID
                    REVENUECATEGORY{
                         FEECATEGORY_ID
                         FEECATEGORY_NAME
                         FEECATEGORY_CODE
                    }
                    SET_REVENUE_FEE_ID
                    REVENUE{
                         FEE_ID
                         FEE_NAME
                    }
                    SYS_STATUS_ID
                    SYS_STATUS{
                          SYS_STATUS_ID
                          SYS_STATUS_NAME  
                    }
                    
                    SET_MODULE_ID
                    MODULE{
                        MODULE_ID
                        MODULE_NAME
                    }
                    SET_LICENSE_TYPE_ID
                    LICENSETYPE{
                        LICENSETYPE_ID
                        LICENSE_TYPE
                    }
                    SET_BUSINESS_TYPE_ID
                    BUSINESSTYPE{
                        BUSINESS_TYPE_ID
                        BUSINESS_TYPE_NAME
                    }
                    RENEWAL
                    PREVIOUS_APPLICATION_NUMBER
                    DRINKS_MANUFACTURED
                    CONTROL_NUMBER
            }
          }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.LiquorRegisters
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Liquor Registration Data');
                }
            })
    }

