import React from 'react';
import { Button, Modal, Form, Col, InputGroup } from 'react-bootstrap';
import { Search } from 'react-feather';

class LiquorRegistrationModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Liquor and Licenses Registration
                        </Modal.Title>
                    </Modal.Header>

                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="LiquorNumber">
                                    <Form.Label>Liquor Number</Form.Label>
                                    <Form.Control type="text"
                                        placeholder="Enter Liquor number"
                                        name="LiquorNumber"
                                        value={this.props.LiquorNumber}
                                        onChange={this.props.changeLiquorNumber}
                                        required
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter Liquor Number.
                                    </Form.Control.Feedback>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="ModuleType">
                                    <Form.Label>Module Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.ModuleDescription}
                                            placeholder="Search Module Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowModuleSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please Select Module Type.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="LicenseType">
                                    <Form.Label>License Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.LicenseType}
                                            placeholder="Search License Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowLicenseType}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please Select License Type.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>

                                {this.props.LicenseType === 'Brewers' ?

                                    <Form.Group as={Col} md="6" controlId="BusniessType" >
                                        <Form.Label>Business Type</Form.Label>
                                        <InputGroup>
                                            <Form.Control
                                                type="text"
                                                value={this.props.BusniessType}
                                                placeholder="Search Busniess Type"
                                                aria-describedby="inputGroupPrepend"
                                            />
                                            <InputGroup.Prepend>
                                                <Button variant="info" size="sm" onClick={this.props.handleShowBusinessType}>
                                                    <Search />
                                                </Button>
                                            </InputGroup.Prepend>
                                        </InputGroup>
                                    </Form.Group>

                                    :
                                    <></>
                                }
                            </Form.Row>

                            <Form.Row>

                                <Form.Group as={Col} md="2" controlId="Renewal">
                                    <Form.Label>Renewal</Form.Label>
                                    <Form.Check
                                        type="switch"
                                        id="custom-switch"
                                        label=""
                                        name="Renewal"
                                        checked={this.props.RENEWAL === '1' ? true : false}
                                        onChange={this.props.changeRenwal}
                                    />
                                </Form.Group>


                                <Form.Group as={Col} md="5" controlId="PreviousApplicationNumber">
                                    <Form.Label>Previous Application Number</Form.Label>
                                    <Form.Control type="number"
                                        placeholder="Enter Previous Application Number"
                                        name="PreviousApplicationNumber"
                                        value={
                                            this.props.RENEWAL === '1' ?
                                                this.props.PreviousApplicationNumber
                                                :
                                                ''
                                        }
                                        onChange={this.props.changePreviousApplicationNumber}
                                    />
                                </Form.Group>

                                <Form.Group as={Col} md="5" controlId="ControlNumber">
                                    <Form.Label>Control Number</Form.Label>
                                    <Form.Control type="text"
                                        placeholder="Enter Control Number"
                                        name="ControlNumber"
                                        value={this.props.ControlNumber}
                                        onChange={this.props.changeControlNumber}
                                    />
                                </Form.Group>

                            </Form.Row>

                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="BusniessName">
                                    <Form.Label>Business Name</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.BusniessName}
                                            placeholder="Search Busniess Name"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowBusniessName}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please Select Business Name.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>

                            <Form.Row>

                                <Form.Group as={Col} md="6" controlId="FeeCategory">
                                    <Form.Label>Revenue Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.FeeCategoryName}
                                            placeholder="Search Revenue Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowFeeCategorySearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please Select Revenue Type.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>

                                <Form.Group as={Col} md="6" controlId="Fee">
                                    <Form.Label>Revenue Name</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.FeeName}
                                            placeholder="Search Revenue Name"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowFees}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please Select Revenue Name.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>

                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="DrinksManufactured">
                                    <Form.Label>Drinks Manufactured</Form.Label>
                                    <Form.Control
                                        as="textarea" rows="2"
                                        placeholder="Enter Drinks Manufactured"
                                        name="DrinksManufactured"
                                        value={this.props.DrinksManufactured}
                                        onChange={this.props.changeDrinksManufactured}
                                    />
                                </Form.Group>

                                <Form.Group as={Col} md="6" controlId="SystemStatusType">
                                    <Form.Label>System Status Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.SystemStatusTypeDescription}
                                            placeholder="Search System Status Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please Select Status.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>


                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Liquor Number
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description_del">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.LiquorNumber} onChange={this.props.changeLiquorNumber} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default LiquorRegistrationModal;